body {font-family: "Poppins";

}

a {
    text-decoration: none;
    color: #111111;
    font-size: 23px;
}

.styles a {
  display: inline-block;
  margin-block: .5rem;
  padding-inline: 1rem;
}

.style-container {
  li {
  background-color: #272343;
    padding-inline: 2rem;
    padding-block: 2rem;
    list-style: none;
    color: #ffffff;
    margin: 1rem;
    h2 {
      font-size: 26px;
      font-weight: normal;
    }
  h4 {
      font-size: 16px;
      font-weight: normal;
  }
  }
}

.style-container  .style-song-title,  h4 {
  margin: 0;
}

.styles a:hover {
  box-shadow: 1px 1px 1px 1px #cccccc;
}

.style-header {
  text-transform: capitalize;
  font-size: 36px;
  margin: 0;
  padding-inline-start: 1rem;
}

.header-container {
  h3 {
    font-weight: 300;
  }

  h3:active {
    font-weight: 700;
  }
  h3 a:active {
    font-weight: 700;
  }
}

.song-container {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  align-items: center;
  background-color: #111111;
  margin-block-end: 1rem;
  color: #ffffff;
  padding: 1rem;
  justify-content: space-between;
  grid-gap: 1rem;
}

.song-title {
  grid-column: 2/4;
  h2, p {
    margin: 0;
  }
}

.like, .unlike {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
  padding-block: .3rem;
  cursor: pointer;
}

.like {
  background-image: url("./assets/up-arrow.svg");
}

.unlike {
  background-image: url("./assets/down-arrow.svg");
}

img {
  width: 50%;
  height: 50%;
}

.header-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.styles {
  text-transform: capitalize;
}

input,select, textarea {
  display: block;
  padding: .7rem;
  margin: 1rem;
  border-radius: 5px;
  border: none;
  box-shadow: 1px 1px 1px 2px #cccccc;
  box-sizing: border-box;
  min-width: 250px;
}

.addSong {
  button {
    margin: 1rem;
    padding-block: 1rem;
    padding-inline : 2.5rem;
    background-color: #FFD803;
    border: none;
    border-radius: 5px;
    button:focus {
      outline: none;
      border: 1px solid #cccccc;
    }
  }
}

ul {
  padding: 0;
}

.shopping-cart, .favorite {
  cursor: pointer;
}

.cart-songs {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  align-items: center;
  div {
    grid-column: 2/5;
  }
}